$mainRed: #e50914;
$secondaryRed: #b81d24;
$mainDark: #111;
$mainLight: #f5f5f1;


.header {
    position: fixed;
    z-index: 10;
    display: flex;
    justify-content: space-between;
    width: 100%;
    height: 68px;
    transition: .6s ease;
    background: linear-gradient(180deg, rgba(0,0,0,1) 0%, rgba(0,0,0,0) 100%); }

.headerBlack {
    background-color: $mainDark; }

nav {
    padding: 22px 0 0 60px; }

.nav_box {
    height: 16px;
    width: 336px;
    display: inline-flex;
    justify-content: space-around;
    margin-left: 35px;
    margin-top: 5px;
    vertical-align: top;
    a {
        color: #fff;
        text-decoration: none; } }

.settings {
    display: flex;
    padding: 17px 55px 0 0;
    vertical-align: top; }

.header_nav__logo {
    object-fit: contain;
    height: 25px; }

.header_settings__logo {
    height: 33px;
    object-fit: contain;
    border-radius: 5px; }

.header_settings__buttons {
    display: flex;
    vertical-align: top;
    margin-top: 5px;
    svg {
        margin-right: 24px; }
    span {
        margin-top: 5px;
        margin-right: 24px; } }

.header_settings__button {
    margin-top: 5px; }
